.card--news--home {
    .card--news--home__anchor {
        display: flex;
        flex-direction: column;
        overflow: hidden;
        @include anchor-reset();
        @include card-anchor-colors();
        background-color: $color-purewhite;

        @include media('reducemotion') {
            transition: none;
        }

        &,
        &:focus,
        &:active,
        &:visited {
            border: none;
            outline-style: $border-style-solid;
        }

        &:hover {
            background-color: inherit;
            @include media('hover') {
                border: none;
            }
            .card--news--home__thumbnail img {
                transform: scale(1.1);
            }
        }
    }

    .card--news--home__thumbnail {
        position: relative;
        line-height: 0;
        overflow: hidden;

        img {
            transition: transform 0.6s cubic-bezier(0.4, 0, 0.2, 1);
        }
    }

    .card--news--home__text {
        padding-bottom: $measure--basic;
        padding-top: $measure--quarter;
        background-color: $color-purewhite;
    }

    .card--news--home__heading {
        @include h5style();
        text-decoration: underline;
        display: inline;
        max-width: unset;
        color: $color-nearblack;
        text-decoration-thickness: 12%;
    }

    .card--news--home__date {
        @include smalltype();
    }
}
