main {
    .home-banner {
        margin: 0;
        position: relative;
        top: 0;
        width: 100%;
        height: 100%;
        background-color: $color-brand-primary;
        background-size: cover;

        .hero-image img,
        video {
            width: 100%;
            height: 100%;
            position: relative;
            overflow: hidden;
            object-fit: cover;

            @include media('<small') {
                min-height: 450px;
            }

            @include media('>=small') {
                min-height: 600px;
            }

            @include media('>=desktop') {
                max-height: 70vh;
            }
        }

        .hero-image__veil {
            position: relative;
            min-height: 100%;

            &:before {
                content: '';
                position: absolute;
                top: 0;
                left: 0;
                z-index: z-index('above');
                background-color: rgba(0, 0, 0, 0.55);
                width: 100%;
                height: 100%;
            }
        }

        .cta-block {
            a {
                display: inline-block;
                @include cta--basic(
                    $color-purewhite,
                    $color-red50,
                    $color-purewhite,
                    $color-red80,
                    $color-nearblack,
                    round
                );

                &:focus,
                &:active {
                    @include anchor-default-outline($color-purewhite);
                }
            }
        }

        .cta-block__cta {
            display: inline-block;
            &:not(:first-of-type) {
                margin-left: $measure--basic;
            }
        }
    }

    .home-banner--video {
        .hero-image {
            position: absolute;
            top: 0;
        }
    }

    .home-banner__content {
        width: 100%;
        height: 100%;
        z-index: z-index('inpage');

        @include standard-grid();

        @include media('>=small') {
            @include standard-box();
        }
    }

    .home-banner__content__text {
        position: absolute;
        top: 50%;
        left: 0;
        grid-column: 1 / -1;
        color: $color-purewhite;
        width: 100%;
        z-index: z-index('inpage');
        @include default-padding();

        p {
            text-shadow: 0px 0px 5px rgba(0, 0, 0, 0.65);
        }

        p,
        ul {
            text-align: left;
        }

        @include media('>=tablet') {
            p,
            ul {
                text-align: center;
            }
        }
    }

    .home-banner__strapline {
        grid-column: 1/-1;
        position: absolute;
        top: 0;
        z-index: z-index('veil');

        @include media('>=small') {
            padding: 0;
        }
    }

    .home-banner__strapline__text {
        padding: $measure--medium3 0;
        background-color: transparent;
        height: $measure--huge5; // 10rem
        width: $measure--huge5;
        @include default-margin();

        @include media('>=tablet') {
            background-color: $color-brand-primary;
            padding: $measure--medium3 $measure--basic; // 3rem 1rem
        }

        @include media('>=small') {
            margin-left: 0;
        }
    }

    .banner__video__button__wrap {
        max-width: $max-line-length;
        @include standard-grid;
        z-index: z-index('above');
        position: absolute;
        top: 0;
        height: 100%;
        right: 0;
        left: 0;
        align-items: end;

        @include media('>=small') {
            @include standard-box();
        }

        @include media('<small') {
            @include default-margin();
        }
    }

    .banner__video__button {
        border-radius: 50%;
        border: $border-width-default solid $color-purewhite;
        color: $color-purewhite;
        height: $measure--medium3; // 3rem
        width: $measure--medium3;
        background-color: $color-gray10;
        z-index: z-index('veil'); // 3
        padding: $measure--half; // 0.5rem
        transition: all 0.2s ease-in-out;
        grid-column: -2/ -1;
        margin-bottom: $measure--small4;

        &:hover {
            @include media('hover') {
                background-color: $color-brand-primary;
                transform: scale(1.2);
            }
        }

        &:focus {
            background-color: $color-brand-primary;
        }

        &:focus,
        &:active {
            outline: $border-width-default $border-style-solid $color-purewhite;
            outline-offset: 3px;
        }

        .button__img--pause,
        .button__img--play {
            margin: auto;
            height: 100%;
        }

        .button__img--pause {
            display: block;
        }
        .button__img--play {
            display: none;
        }

        &.play {
            .button__img--pause {
                display: none;
            }
            .button__img--play {
                display: block;
            }
        }
    }
}

.js {
    main {
        .home-banner--video {
            video::-webkit-media-controls-overlay-play-button {
                display: none;
                -webkit-appearance: none;
            }

            video::-webkit-media-controls-panel {
                display: none !important;
                -webkit-appearance: none;
            }

            video::-webkit-media-controls-play-button {
                display: none !important;
                -webkit-appearance: none;
            }

            video::-webkit-media-controls-start-playback-button {
                display: none !important;
                -webkit-appearance: none;
            }

            video::-webkit-media-controls {
                display: none !important;
                -webkit-appearance: none;
            }
        }
    }
}

.no-js {
    main {
        .home-banner__strapline {
            position: absolute;
            top: 0;
            left: 0;
        }

        .home-banner__content {
            position: initial;
        }

        .home-banner--video {
            .hero-image__veil:before {
                content: none;
            }
        }
    }
}
