.home-banner--promo {
    overflow: hidden;
    position: relative;

    .home-banner--promo__container {
        max-width: $max-line-length;
        @include standard-grid;
        z-index: z-index('above');
        position: relative;

        @include media('>=small') {
            @include standard-box();
        }

        @include media('<small') {
            @include default-margin();
        }
    }

    .home-banner--promo__image {
        position: absolute;
        left: 0;
        right: 0;
        height: 100%;
        top: 0;

        figure {
            margin-top: 0;
        }

        @include media('<desktop') {
            picture {
                display: flex;
                justify-content: center;
            }
        }

        img {
            width: 100%;

            @include media('>between', '<desktop') {
                height: 100%;
                max-width: inherit;
                width: auto;
            }
        }
    }

    .home-banner--promo__content {
        grid-column: 1/10;
        background-color: $color-purewhite;
        color: $color-gray10;
        margin-top: $measure--huge3;
        margin-bottom: $measure--large4;
        padding: $measure--medium3;

        @include media('<desktop') {
            grid-column: 1/12;
        }

        @include media('<between') {
            grid-column: 1/-1;
            margin-bottom: $measure--medium5;
            margin-top: 45%;
        }

        @include media('<tablet') {
            margin-top: 42%;
        }

        @include media('<tablet') {
            margin-top: 35%;
        }
    }

    .home-banner--promo__content__heading {
        margin-top: $measure--basic;
        @include thetatype();
    }

    .home-banner--promo__content__text,
    .cta-block {
        margin-top: $measure--small2;
    }

    .cta-block {
        display: flex;
        flex-direction: row;
        gap: $measure--basic;
    }

    & ~ .search-box__wrapper .search-box__cta__wrapper {
        display: none;
    }

    ~ .search-box__wrapper .search-box__text__wrapper {
        padding-bottom: $measure--medium7;
    }

    & ~ * {
        position: relative;
        background: $color-purewhite;
    }

    &.home-banner__theme--default {
        .home-banner--promo__content__heading {
            @include media('>desktop') {
                font-size: $measure--medium3;
                line-height: $measure--medium1;
            }
        }
    }

    &.home-banner__theme--undergraduate {
        .home-banner--promo__content {
            background: $color-purewhite;
            border: $border-width-hairline $border-style-solid $color-gray4;
            box-shadow: $measure--basic $measure--basic 0rem rgba(0, 0, 0, 0.3);
        }

        .home-banner--promo__content__heading {
            color: $color-purewhite;
            background: $color-red50;
            box-decoration-break: clone;
            -webkit-box-decoration-break: clone;
            display: inline;
            padding: 0rem 0.5rem;
            @include zetatype();

            @include media('>=tablet', '<=between') {
                @include etatype();
            }

            @include media('>between') {
                @include thetatype();
            }

            line-height: $measure--medium3;

            @include media('>=tablet') {
                line-height: $measure--medium5;
            }
        }

        .home-banner--promo__content__text {
            color: $color-nearblack;
        }

        .cta-block {
            a {
                &:focus,
                &:active {
                    @include anchor-default-outline($color-nearblack);
                }
            }
        }
    }
}

main {
    .home-banner--promo {
        .cta-block {
            a {
                &:focus,
                &:active {
                    @include anchor-default-outline($color-nearblack);
                }
            }
        }
    }
}
