.sr-only-focusable-within:active, .sr-only-focusable-within:focus, .sr-only-focusable-within:focus-within {
    clip: auto;
    height: auto;
    margin: 0;
    overflow: visible;
    position: static;
    width: auto;
}

/**
    Used where card has two divs, one most likely
    being a thumbnail.
**/
.heading-centered {
    text-align: center;
}

.heading-centered:after {
    content: '';
    width: 2.5rem;
    height: 0.25rem;
    display: block;
    background: #d61726;
    margin: 0.25rem auto 1rem auto;
}

/**
 *  Example HTML
 *
 *  <div class="wrapper--text-highlight">
 *      <p class="text-highlight"><span>Highlighted text</span></p>
 *  </div> 
**/
.wrapper--text-highlight {
    max-width: 40ch;
}

.wrapper--text-highlight .text-highlight {
    background: #832c9e;
    color: #ffffff;
    font-weight: 500;
    display: inline;
    line-height: 2rem;
    box-shadow: 0.5rem 0 0 #832c9e, -0.5rem 0 0 #832c9e;
    box-decoration-break: clone;
}

html {
    text-size-adjust: 100%;
}

body {
    margin: 0;
}

h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
figure,
ol,
ul,
dl,
dt,
dd,
fieldset {
    border: 0;
    margin: 0;
    padding: 0;
}

main {
    display: block;
}

h1,
h2,
h3,
h4,
h5,
h6 {
    font-size: inherit;
}

b,
strong {
    font-weight: 700;
}

a,
button {
    color: inherit;
}

a {
    text-decoration: none;
}

button {
    background: none;
    border: 0;
    cursor: pointer;
    font: inherit;
    letter-spacing: inherit;
    overflow: visible;
    padding: 0;
    touch-action: manipulation;
}

label,
select {
    cursor: pointer;
}

::-moz-focus-inner {
    padding: 0;
    border: 0;
}

img {
    max-width: 100%;
    height: auto;
    border: 0;
}

iframe {
    display: block;
    margin: 1rem;
}

address {
    font-style: inherit;
}

