main {
    .home__research {
        // Generic
        padding-top: $measure--half;
        background: $color-purewhite;

        .wrap--research__content {
            background: $color-gray4;
            padding-bottom: $measure--small4;
        }

        .wrap--research__links {
            h3 {
                color: $color-gray10;
            }
        }

        @include media('>=large') {
            .wrap--research__content {
                padding-bottom: $measure--medium11;
            }
        }

        .research__content {
            color: $color-purewhite;
        }

        .research__heading,
        .research__intro,
        .research__links {
            @include heading-font-features();
            @include font-family('heading');
        }

        .research__heading,
        .research__intro {
            grid-column: 1/-1;
        }

        .research__intro {
            margin-bottom: $measure--medium7;
            @include betatype();
        }

        .wrap--research__media {
            margin-top: -$measure--small5;
            position: relative;
            height: 100%;
            background: $color-gray4;

            .youtube-video__wrapper {
                padding-bottom: 56.25%;
                position: relative;
                iframe {
                    height: 100%;
                    margin: 0;
                    position: absolute;
                    width: 100%;
                }
            }

            .youtube-video {
                margin-top: 0;
            }

            .media__container {
                position: absolute;
                width: 100%;
                height: 100%;

                &.in {
                    visibility: visible;
                    opacity: 1;
                    transition: visibility $transition-duration--long / 2,
                        opacity $transition-duration--long / 2 linear;
                }

                &.out {
                    visibility: hidden;
                    opacity: 0;
                    transition: visibility $transition-duration--long / 2,
                        opacity $transition-duration--long / 2 linear;
                }
            }
        }

        .media__container__img {
            object-fit: cover;
            width: 100%;
        }

        .research__links {
            list-style-type: none;
            margin: 0;
        }

        .linked-heading {
            display: block;
            &:active,
            &:focus {
                outline: $border-width-default $border-style-solid $color-gray11;
            }

            &:focus {
                transition: none;
                background-image: none;
                background-size: 0;

                .linked-heading__text {
                    &:before {
                        opacity: 1;
                        width: 100%;
                    }
                }

                .linked-heading__icons {
                    span:nth-child(1) {
                        display: none;
                        visibility: hidden;
                        opacity: 0;
                        animation: slide-right-out 600ms;
                    }

                    span:nth-child(2) {
                        display: inherit;
                        visibility: visible;
                        opacity: 1;
                        animation: slide-left-in 600ms;
                    }
                }
            }
        }

        .linked-heading__text {
            color: $color-gray10;
            border-bottom-color: $color-gray4;
        }

        .linked-heading__icons {
            background: transparent;
            color: $color-brand-primary;
            width: $measure--small2;
            height: $measure--small2;
            position: absolute;
            right: 0;
            transform: translateY(-50%);
            top: 50%;

            span {
                &:nth-child(1),
                &:nth-child(2) {
                    font-size: $font-size--icon;
                }
            }
        }

        @include media('<tablet') {
            .research__content {
                flex-direction: column;
            }

            .wrap--research__media {
                margin-bottom: -$measure--basic;
                margin-left: -$measure--basic;
                margin-right: $measure--small4;

                .media__container {
                    &.in,
                    &.out {
                        position: relative;

                        &:first-of-type {
                            opacity: 1;
                            visibility: visible;
                        }

                        &:not(:first-of-type) {
                            display: none;
                        }
                    }
                }
            }
        }

        @include media('<small') {
            .research__content {
                @include default-margin();
                display: flex;
                gap: $measure--basic;
                > * {
                    flex: 1;
                }
            }

            .wrap--research__intro {
                @include default-margin();
            }

            .media__container__img {
                height: 100%;
            }
        }

        @include media('>=tablet') {
            .wrap--research__media {
                grid-column: 1/11;
                height: auto;
            }

            .research__content {
                @include content-grid;
            }

            .wrap--research__links {
                grid-column: 12/-2;
                margin-top: $measure--small2;
            }
        }

        @include media('>=tablet', '<small') {
            .wrap--research__content {
                padding-bottom: $measure--medium3;
            }
        }

        @include media('>=small') {
            .wrap--research__intro,
            .research__content {
                @include standard-box();
            }

            .media__container__img {
                width: 100%;
            }

            .wrap--research__content {
                padding-bottom: $measure--large4;
            }
        }
    }
}
