@mixin selected-select() {
    background-color: $color-purewhite;
    border-color: $color-nearblack;
    color: $color-nearblack;
    font-weight: $font-weight-medium;
}

#search-box {
    form {
        @include media('>=small') {
            @include standard-box();
            @include standard-grid();

            grid-template-rows: repeat(3, auto);
        }

        h2 {
            grid-column: 1 / -1;
        }

        .search-box__filter {
            grid-column: 1 / span 6;
        }

        .search-box__query {
            grid-column: 7 / span 10;
        }

        select {
            padding-right: $measure--basic;
        }
    }
}

.search-box__text__wrapper {
    background-color: $color-brand-primary;
    padding-bottom: $measure--small4;

    @include media('<small') {
        padding-left: $measure--basic;
        padding-right: $measure--basic;
    }

    .search-box__text {
        @include media('>=small') {
            @include standard-box();
        }
    }
}

.search-box__cta__wrapper {
    background-color: $color-brand-primary;
    padding-bottom: $measure--basic;

    @include media('<small') {
        padding-left: $measure--basic;
        padding-right: $measure--basic;
    }

    .search-box__cta__wrapper__border {
        @include media('>=small') {
            @include standard-box();
        }
    }
}

#search-box {
    background-color: $color-brand-primary;
    padding-top: $measure--small4;
    padding-bottom: $measure--small4;

    @include media('<small') {
        padding-left: $measure--basic;
        padding-right: $measure--basic;
    }

    h2 {
        color: $color-purewhite;
        padding-bottom: $measure--basic;
        margin-top: initial;
    }
}

.search-box__select {
    color: $color-nearblack;
    height: auto;
    position: relative;

    @include media('<small') {
        margin-bottom: $measure--basic;
    }

    label {
        @include smalltype();

        color: $color-red50;
        display: block;
        font-weight: $font-weight-text;
        padding-bottom: $measure--basic;
    }

    select {
        @include font-family('default');
        @include body-font-features();
        @include alphatype();

        background-color: $color-purewhite;
        border-color: $color-nearblack;
        border-style: $border-style-solid;
        border-width: $border-width-default;
        border-radius: $measure--quarter;
        bottom: $measure--quarter;
        color: $color-nearblack;
        font-weight: $font-weight-text;
        height: $measure--medium4;
        width: 100%;
        padding-left: 0.5rem;
        padding-right: 0.5rem;
        -webkit-appearance: none;
        -moz-appearance: none;
        appearance: none;

        &:focus {
            @include outline-properties($color-outline--dark);
            outline-style: $border-style-solid;
        }

        &:hover {
            @include media('hover') {
                background-color: $color-nearblack;
                color: $color-purewhite;
                font-weight: $font-weight-medium;
                transition: all $transition-duration ease;

                @include media('reducemotion') {
                    transition: none;
                }
            }
        }
    }

    .fa-chevron-down {
        position: absolute;
        right: $measure--basic;
        top: $measure--threequarters;
        background-color: $color-nearblack;
        color: $color-purewhite;
        border-radius: 100%;
        padding: $measure--half;
        pointer-events: none;
        min-width: 30px;
        min-height: 30px;
    }

    &:not(.search-box__select--sort) {
        &.search-box__select--selected {
            select {
                @include selected-select();
            }
        }
    }

    &.search-box__select--sort {
        &.search-box__select--selected {
            select {
                @include media('<tablet') {
                    @include selected-select();
                }
            }
        }
    }
}

.search-box__query {
    & > div {
        display: flex;
        flex-direction: row;
        flex-wrap: nowrap;
    }
}

.search-box__query__input {
    @include font-family('default');
    @include body-font-features();
    @include alphatype();

    display: flex;
    flex-grow: 1;
    font-weight: $font-weight-text;
    padding: 0;
    position: relative;

    &:focus-within {
        @include outline-properties($color-purewhite);
        outline-style: $border-style-solid;
    }

    input {
        @include font-family('default');
        @include body-font-features();
        @include alphatype();

        border-color: $color-nearblack;
        border-style: $border-style-solid;
        border-width: $border-width-default;
        border-radius: $measure--quarter 0 0 $measure--quarter;
        font-style: normal;
        height: $measure--medium4;
        min-width: 0; // Constrains width in Firefox
        padding-left: $measure--half;
        padding-right: $measure--medium5;
        width: 100%;

        @include media('<mobile') {
            padding-right: $measure--basic;
        }

        &:focus-within {
            color: $color-gray10;
            font-style: normal;
            font-weight: $font-weight-medium;
            outline: 0;
        }

        &::placeholder {
            font-style: italic;
        }

        &:placeholder-shown {
            color: $color-gray7;
        }
    }

    .search-box__clear {
        @include epsilontype();

        align-items: center;
        display: flex;
        flex-direction: row;
        flex-wrap: nowrap;
        height: $measure--small5;
        justify-content: center;
        position: absolute;
        right: $measure--half;
        top: $measure--quarter;
        width: $measure--small5;

        &:focus {
            @include outline-properties();
            outline-style: $border-style-solid;
            outline-offset: -#{$border-width-thicker};
        }

        .search-box__clear__text {
            @include sr-only();
        }

        .icon {
            /*
            // temp overide with xmark-temp-styling below

            --fa-primary-color: #{$color-gray7};
            --fa-primary-opacity: 1;
            --fa-secondary-color: #{$color-gray1};
            --fa-secondary-opacity: 1;
            */
            @include xmark-temp-styling;

            letter-spacing: 0;
            line-height: 1;
        }

        &:hover {
            @include media('hover') {
                background-color: transparent;

                .icon {
                    /*
                    // temp overide with xmark-temp-styling below

                    --fa-primary-color: #{$color-gray1};
                    --fa-secondary-color: #{$color-gray7};
                    */
                    @include xmark-temp-styling-hover;
                }
            }
        }
    }
}

.search-box__query__suggestions {
    background-color: $color-purewhite;
    border-radius: 0 0 $measure--half $measure--half;
    border-top: 0;
    border: $border-width-hairline $border-style-solid $color-gray2;
    box-shadow: rgba(0, 0, 0, 0.25) 0 $measure--basic $measure--medium3 -$measure--threequarters;
    color: $color-purewhite;
    font-weight: $font-weight-bold;
    left: 0;
    list-style: none;
    margin-left: 0;
    max-width: unset;
    position: absolute;
    top: $measure--medium1;
    width: 100%;
    z-index: z-index('above');

    li {
        font-weight: $font-weight-text;
        margin-top: 0;

        button {
            color: $color-text;
            height: 100%;
            padding: $measure--half $measure--small4 $measure--half
                $measure--small4;
            text-align: left;
            width: 100%;

            &:focus {
                outline-color: $color-nearblack;
                outline-offset: (-$border-width-default);
                outline-style: $border-style-dotted;
                outline-width: $border-width-default;
                text-decoration: underline;
            }

            &:active {
                @include outline-properties();
                outline-style: $border-style-solid;
                outline-offset: 0;
            }

            &:hover {
                @include media('hover') {
                    background-color: $color-linkblue;
                    color: $color-purewhite;
                    text-decoration: underline;
                }
            }

            .search-box__suggestion {
                font-weight: $font-weight-bold;
            }
        }
    }
}

.search-box__query__suggestions.hide {
    display: none;
}

.finder__clear {
    @include epsilontype();

    align-items: center;
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    height: $measure--small5;
    justify-content: center;
    position: absolute;
    right: $measure--half;
    top: $measure--quarter;
    width: $measure--small5;

    &:focus {
        @include outline-properties();
        outline-style: $border-style-solid;
        outline-offset: -#{$border-width-thicker};
    }

    .finder__clear__text {
        @include sr-only();
    }

    .icon {
        /*
        // temp overide with xmark-temp-styling below

        --fa-primary-color: #{$color-gray7};
        --fa-primary-opacity: 1;
        --fa-secondary-color: #{$color-gray1};
        --fa-secondary-opacity: 1;
        */
        @include xmark-temp-styling;

        letter-spacing: 0;
        line-height: 1;
    }

    &:hover {
        @include media('hover') {
            background-color: transparent;

            .icon {
                /*
                // temp overide with xmark-temp-styling below

                --fa-primary-color: #{$color-gray1};
                --fa-secondary-color: #{$color-gray7};
                */
                @include xmark-temp-styling-hover;
            }
        }
    }
}

.search-box__query__submit {
    font-size: $font-size--delta;
    background-color: $color-nearblack;
    color: $color-purewhite;
    padding-left: $measure--basic;
    padding-right: $measure--basic;
    border-color: $color-nearblack;
    border-style: $border-style-solid;
    border-width: $border-width-default;
    border-radius: 0 $measure--quarter $measure--quarter 0;

    &:focus {
        @include outline-properties($color-outline--dark);
        outline-style: $border-style-solid;
    }

    .search-box__query__submit__icon {
        font-weight: $font-weight-medium;
    }
}

.search-box__text {
    color: $color-purewhite;

    a {
        @include anchor-reset-properties;
        color: $color-purewhite;
        text-decoration: underline;
        border-bottom: 0;
        text-decoration: underline;

        &:active {
            @include anchor-reset-properties;
        }

        &:hover {
            @include media('hover') {
                @include anchor-reset-properties;
                text-decoration: underline;
                color: $color-purewhite;
                border-bottom: 0;
            }
        }

        &:visited,
        &:focus,
        &:active {
            color: $color-purewhite;
            border-bottom: 0;
            text-decoration: underline;
        }

        &:focus,
        &:active {
            @include outline-properties($color-outline--dark);
            outline-style: $border-style-solid;
        }
    }

    .search-box__text__back-slash {
        padding-left: $measure--small4;
        padding-right: $measure--small4;
        color: $color-gray2;
        position: relative;

        &:before {
            background: $color-gray2;
            content: '';
            display: inline-block;
            height: $measure--small3;
            position: absolute;
            -webkit-transform: rotate(-45deg);
            -moz-transform: rotate(-45deg);
            -ms-transform: rotate(-45deg);
            -o-transform: rotate(-45deg);
            transform: rotate(-45deg);
            width: $border-width-hairline;
        }

        @include media('>=mobile', '<small') {
            padding-left: $measure--small2;
            padding-right: $measure--small3;
        }
    }

    @include media('>=mobile') {
        p {
            display: inline;
        }
    }

    @include media('<mobile') {
        p:first-child {
            margin-top: initial;
        }

        span {
            display: none;
        }
    }
}

.search-box__cta {
    padding-top: $measure--basic;
    border-top: rgba(0, 0, 0, 0.25) $border-width-hairline $border-style-solid;

    @include media('>tablet') {
        display: flex;
        align-items: center;
    }

    .search-box__cta--text {
        display: flex;
        align-items: center;
    }

    .fa-link {
        color: $color-purewhite;
        background-color: $color-nearblack;
        border-radius: 100%;
        padding: $measure--threequarters;
    }

    .search-box__cta__heading {
        color: $color-purewhite;
        margin-top: $measure--threequarters;
        margin-left: $measure--half;
        font-weight: $font-weight-bold;
    }

    ul {
        display: flex;
        flex-wrap: wrap;
        list-style: none;
        max-width: initial;
        margin-top: initial;
        margin-left: $measure--basic;

        @include media('<=tablet') {
            margin-left: initial;
        }

        li {
            margin-top: initial;
            margin-left: $measure--basic;
            margin-top: $measure--half;
            margin-right: $measure--basic;

            @include media('<=tablet') {
                margin-left: initial;
            }
        }
    }
}
