main {
    .home__promo {
        position: relative;
        background-size: cover;

        &[data-position='right'] {
            .wrap--home__promo__card {
                margin-left: auto;
            }

            .triangle__block--right {
                display: none;
            }
        }

        &[data-position='left'] {
            .triangle__block--left {
                display: none;
            }
        }

        .home__promo__text p {
            @include heading-font-features();
            @include font-family('heading');
        }

        .wrap--home__promo__card {
            padding: $measure--large2 0;
            box-sizing: border-box;
            transition: transform 0.3s, opacity 0.3s;
            z-index: z-index('above');
            width: 50%;
        }

        .home__promo__title {
            margin-top: 0;
        }

        .home__promo__img {
            width: 100%;
        }

        .home__promo__card {
            position: relative;
            z-index: z-index('above');
            background-color: $color-purewhite;
            padding: $measure--medium1;
            box-shadow: 0 2px RGB(0 0 0 / 2%), 0 0 6px RGB(0 0 0 / 3%),
                0 0 10px RGB(0 0 0 / 4%), 0 0 18px RGB(0 0 0 / 4%),
                0 0 34px RGB(0 0 0 / 5%), 0 0 80px RGB(0 0 0 / 7%);
        }

        .basic-cta--bright {
            &:focus,
            &:active {
                outline-style: $border-style-solid;
            }
        }

        .triangle__block {
            bottom: 0;
            position: absolute;

            &.triangle__block--left {
                margin-bottom: 0;
                transform: rotate(270deg);
                left: -1px;
            }

            &.triangle__block--right {
                right: 0;
                transform: rotate(180deg);
            }
        }

        // Below viewports
        @include media('<small') {
            .home__promo__container {
                @include default-margin();
            }
        }

        @include media('<tablet') {
            .wrap--home__promo__card {
                width: 75%;
            }
        }

        @include media('<mobile') {
            background-image: url('https://www.city.ac.uk/__data/assets/image/0008/720674/student-life-mobile.jpg');
            .wrap--home__promo__card {
                width: 100%;
            }
        }

        // Above viewports
        @include media('>=small') {
            .home__promo__container {
                @include standard-box();
            }

            .wrap--home__promo__card {
                &.in-view {
                    top: 50%;
                    opacity: 1;
                    transform: translateY(0%);
                }
                &:not(.in-view) {
                    opacity: 0;
                    transform: translateY(100%);
                }
            }
        }
    }
}

.no-js {
    main {
        .home__promo {
            .wrap--home__promo__card {
                opacity: 1;
                transform: translateY(0px);
            }
        }
    }
}

.home__promo__img {
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    min-height: auto;

    img {
        position: absolute;
        width: 100%;
        height: 100%;
        top: 0;
        object-fit: cover;
        object-position: top;
    }
}
