.home-banner--image {
    .hero-image__veil {
        background-image: linear-gradient(
            180deg,
            rgba(0, 0, 0, 0.7),
            rgba(0, 0, 0, 0.4) 30%
        );
    }

    .home-banner__content__text {
        display: flex;
        flex-direction: column;
        align-items: center;

        @include media('<tablet') {
            top: 30%;
        }

        .banner__subtitle {
            @include heading-font-features();
            @include font-family('heading');
            @include betatype();
        }
    }
}
