@import 'home-vi-sections/banner';
@import 'home-vi-sections/banner-promo';
@import 'home-vi-sections/banner-hero';
@import 'home-vi-sections/pathway';
@import 'home-vi-sections/social-image-grid';
@import 'home-vi-sections/news-events';
@import 'home-vi-sections/promo';
@import 'home-vi-sections/research';

.home-new-vi {
    // body class override, development only

    .breadcrumbs {
        display: none;
    }

    .navigation--secondary {
        display: none;
    }

    .triangle__block {
        width: $measure--huge5;
    }

    .triangle__block--left {
        margin-bottom: -$measure--huge3;
    }

    .triangle__block--right {
        right: 0;
        position: absolute;
        transform: rotate(180deg);
        margin-top: -$measure--huge5;
    }
}

.home-new-vi {
    main {
        margin: 0;
        padding: 0;
        p {
            max-width: $max-line-length;
        }

        .outline-cta--rounded-edges {
            margin-bottom: 0;
        }
    }
}
