.card--event--home {
    .card--event--home__anchor {
        display: flex;
        flex-direction: column;
        overflow: hidden;
        @include anchor-reset();
        @include card-anchor-colors();
        background-color: $color-purewhite;

        @include media('reducemotion') {
            transition: none;
        }

        &,
        &:focus,
        &:active,
        &:visited {
            border: none;
            outline-style: $border-style-solid;
        }

        &:hover {
            background-color: inherit;
            @include media('hover') {
                border: none;
            }

            .card--event--home__thumbnail img {
                transform: scale(1.1);
            }
        }
    }

    .card--event--home__thumbnail {
        position: relative;
        line-height: 0;
        overflow: hidden;

        img {
            transition: transform 0.6s cubic-bezier(0.4, 0, 0.2, 1);
        }
    }

    .card--event--home__date-label {
        position: absolute;
        @include betatype();
        align-self: flex-start;
        background-color: $color-red50;
        color: $color-purewhite;
        display: inline-block;
        font-weight: $font-weight-bold;
        left: 0;
        bottom: 0;
        margin-top: 0;
        position: absolute;
        padding: $measure--half $measure--basic;
        display: flex;
        flex-direction: column;
        text-align: center;
        @include heading-style();

        @include media('reducemotion') {
            transition: none;
        }

        span {
            font-weight: $font-weight-text;
            margin-left: auto;
            margin-right: auto;
        }
    }

    .card--event--home__text {
        padding-bottom: $measure--basic;
        padding-top: $measure--quarter;
        background-color: $color-purewhite;
    }

    .card--event--home__heading {
        @include h5style();
        text-decoration: underline;
        display: inline;
        max-width: unset;
        color: $color-nearblack;
        text-decoration-thickness: 12%;
    }

    .card--event--home__date {
        @include smalltype();
        margin-top: $content-element-margin;
    }
}
