main {
    .home__news-events__wrap {
        background: $color-purewhite;
    }

    .home__news-events {
        display: flex;
        gap: $measure--basic;
        padding-top: $measure--medium11;
        padding-bottom: $measure--medium11;

        @include media('>=small') {
            @include standard-box();
        }

        @include media('<small') {
            @include default-margin();
        }

        @include media('<tablet') {
            flex-direction: column;
        }

        .home__news-events__section {
            flex-basis: 50%;

            .related-content {
                max-width: inherit;
            }

            @include media('<tablet') {
                flex-basis: 100%;
            }
        }

        .home__news-events__header {
            display: flex;
            border-bottom: $border-width-default $border-style-solid
                $color-brown5;
            justify-content: space-between;
            padding-bottom: $measure--half;

            h2 {
                margin: 0;
            }
        }

        .home__news-events__header__link {
            display: flex;
            align-items: flex-end;
            margin-bottom: $measure--quarter;

            a {
                @include anchor-inline(
                    $color-red50,
                    $color-red50,
                    $color-purewhite,
                    $color-red50
                );
            }
        }

        .related-content {
            display: flex;
            list-style: none;
            gap: $measure--basic;
            flex-wrap: inherit;

            @include media('<mobile') {
                flex-direction: column;
            }

            .card--news--home,
            .card--event--home {
                flex-basis: 50%;
                margin: 0;

                @include media('<mobile') {
                    flex-basis: 100%;
                }
            }
        }
    }
}

.no-js {
    main {
        .home__news-events {
            .related-content {
                margin-left: 0;
            }
        }
    }
}
